import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { BrowseComponent } from './browse/browse.component';
import { ListingsComponent } from './listings/listings.component';
import { AuthComponent } from './home/auth/auth.component';
import { BikeDashboardComponent } from './bike-dashboard/bike-dashboard.component';
import { BikeDetailComponent } from './bike-dashboard/bike-detail/bike-detail.component';
import { ListingsNewComponent } from './listings/listings-new/listings-new.component';
import { ListingsDashboardComponent } from './listings/listings-dashboard/listings-dashboard.component';
import { BikeService } from './services/bike.service';
import { ListingComponent } from './listings/listings-dashboard/listing/listing.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    BrowseComponent,
    ListingsComponent,
    AuthComponent,
    BikeDashboardComponent,
    BikeDetailComponent,
    ListingsNewComponent,
    ListingsDashboardComponent,
    ListingComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    HttpModule
  ],
  providers: [BikeService],
  bootstrap: [AppComponent]
})
export class AppModule { }



